.MainClass {
  position: fixed;
  height: 100vh;
  width: 100%;
  padding: 0px;
  margin: 0px;
  top: 0;
  left: 0;
  pointer-events: none;
  font-family: "Bebas Neue", cursive;
}

.TimeCard {
  position: absolute;
  top: 15%;
  left: 0px;
  color: white;
  font-size: 6vh;
  width: 100%;
  text-align: center;
  background-color: #00000033;
}

.RestartButton {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 40%;
  left: 0;
  width: 100%;
  color: white;
  font-size: 80px;
  background-color: #00000033;
  pointer-events: auto;
  cursor: pointer;
}

.keyBoardInputs {
  position: absolute;
  bottom: 20px;
  left: 50%;
  transform: translate(-50%, 0);
}

.key {
  width: 60px;
  height: 60px;
  border: 2px solid white;
  border-radius: 5px;
  background-color: #ffffff44;
  margin: 5px;
  transition: 0.2s background-color ease-in;
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
}

.large {
  width: 208px;
}

.active {
  background-color: rgba(255, 255, 255, 0.8);
}
